<template lang="">
  <div id="games">
    <Search
      :set-default="setDefault"
      :img-src="require(`../assets/img/${myImg}.png`)"
      :img-alt="myImgAlt"
      :my-header-style="{ backgroundColor: bgColor, filter: bgFilter }"
      :my-btn-style="{ backgroundColor: btnBgColor, color: btnColor }"
      :placeholder="placeholder"
    />
    <ConnectionStatus
      :database="database"
      :link-db="link.href"
      :status="status"
      :child-class="status === 'OK' ? 'green' : ''"
    />
    <Jumbotron
      :jumbo-bg="{
        backgroundImage: `url(${jumboBg})`,
        backgroundRepeat: 'no-repeat',
        backgroundColor: '#000000',
        backgroundPosition: 'center',
        backgroundSize: 'cover',
      }"
      :jumbo-title-style="{ color: '#cecece' }"
      :jumbo-title="jumboTitle"
      :jumbo-subtitle-style="{ color: '#7a8288' }"
      :jumbo-subtitle="jumboSubtitle"
    />
    <Main
      class="main"
      @read-more="readMore"
      :welcome="welcome"
      :loading="loading"
      :main-list="displayedMainList"
      :detail-card="detailCard"
      :page-number="pageNumber"
      :pages="pages"
      :page="page"
      :perPage="perPage"
      @pag-prec="pagPrec"
      @pag-succ="pagSucc"
      @first-page="firstPage"
      @last-page="lastPage"
      @current-page="currentPage"
      @set-main-list="setmainList"
      @paginate-list="paginateList"
    />
  </div>
</template>

<script>
// import axios from "axios";
import Search from "@/components/GamesDB/Search.vue";
import ConnectionStatus from "@/components/SharedComponent/ConnectionStatus.vue";
import Jumbotron from "@/components/SharedComponent/Jumbotron.vue";
import Main from "@/components/GamesDB/Main";
import axios from "axios";

export default {
  name: "GamesdbView",
  components: {
    Search,
    ConnectionStatus,
    Jumbotron,
    Main,
  },
  data() {
    return {
      loading: false,
      welcome: true,
      myImg: "games_db",
      myImgAlt: "GamesDB",
      bgColor: "#303030",
      bgFilter: "brightness(1)",
      btnBgColor: "#9147ff",
      btnColor: "#ffff",
      placeholder: "Find your games...",
      database: "FreeToGame",
      link: { name: "FreeToPlay", href: "https://www.freetogame.com/" },
      status: null,
      jumboBg: require("../assets/img/gamesdb/got.jpg"),
      jumboTitle: "Discover the best free-to-play games!",
      jumboSubtitle:
        "Track what you've played and search for what to play next! Plus get free premium loot!",
      // API
      apiURL: "https://free-to-play-games-database.p.rapidapi.com/api/",
      key: process.env.VUE_APP_GAMESDB_API_KEY,
      host: process.env.VUE_APP_GAMESDB_API_HOST,
      apiPopular: "games",
      apiId: "game?id=",
      mainList: [],
      detailCard: {},
      id: null,

      // PAGINATION
      pageNumber: 0,
      pages: [],
      page: 1,
      perPage: 10,
    };
  },
  computed: {
    displayedMainList() {
      return this.paginateList(this.mainList);
    },
  },
  watch: {
    mainList() {
      this.setmainList();
    },
  },
  methods: {
    setPopular() {
      this.status = "loading";
      this.loading = true;
      let config = {
        headers: {
          "X-RapidAPI-Key": this.key,
          "X-RapidAPI-Host": this.host,
        },
      };
      axios
        .get(this.apiURL + this.apiPopular, config)
        .then((response) => {
          this.status = response.status === 200 ? "OK" : "";
          this.mainList = response.data;
          this.loading = false;
        })
        .catch((err) => {
          console.log(err);
          this.loading = false;
        });
    },
    readMore(id) {
      this.status = "loading";
      this.loading = true;
      this.id = id;
      this.welcome = false;
      let config = {
        headers: {
          "X-RapidAPI-Key": this.key,
          "X-RapidAPI-Host": this.host,
        },
        params: { id: this.id },
      };
      axios
        .get(this.apiURL + this.apiId, config)
        .then((response) => {
          this.status = response.status === 200 ? "OK" : "";
          this.detailCard = response.data;
          this.loading = false;
        })
        .catch((err) => {
          console.log(err);
          this.loading = false;
        });
    },
    // PAGINATION
    pagPrec() {
      this.page--;
    },
    pagSucc() {
      this.page++;
    },
    firstPage() {
      this.currentPage(this.pages[0]);
    },
    lastPage() {
      this.currentPage(this.pages[this.pages.length - 1]);
    },
    currentPage(page) {
      this.page = page;
    },
    setmainList() {
      const numberOfPages = Math.ceil(this.mainList.length / this.perPage);
      for (let i = 1; i <= numberOfPages; i++) {
        this.pages.push(i);
      }
    },
    paginateList(mainList) {
      const page = this.page;
      const perPage = this.perPage;
      const from = page * perPage - perPage;
      const to = page * perPage;
      return mainList.slice(from, to);
    },
    // RESET
    reset() {
      this.welcome = true;
      this.firstPage();
    },
    //funzione per la situazione di default,viene passata come prop per il bottone reset
    setDefault() {
      this.reset();
    },
  },
  beforeCreate() {},
  created() {
    this.setPopular();
  },
  beforeMount() {},
  mounted() {
    window.scrollTo(0, 0);
  },
  beforeUpdate() {},
  updated() {},
  beforeUnmount() {},
  unmounted() {},
};
</script>

<style lang="scss" scoped>
@import "../assets/style/app.scss";

#games {
  overflow: hidden;
  min-height: 100vh;
  background-color: $body-bg;

  @media all and (max-width: 1400px) {
