<template>
  <header 
    class="d-flex align-items-center ps-5 pe-5" 
    :style="myHeaderStyle"
  >
    <div class="logo">
      <button 
        class="bool_btn" 
        @click.prevent="resetAll()"
      >
        <img 
          :src="imgSrc" 
          :alt="imgAlt" 
        />
      </button>
    </div>
    <div>
      <form class="d-flex mt-3 mb-3 myform">
        <div>
          <!-- <input
            v-model.trim="apiQuery"
            class="form-control"
            type="text"
            :placeholder="placeholder"
          />
        </div>
        <div class="">
          <button
            @click.prevent="sendQuery()"
            class="btn mc_btn"
            :style="myBtnStyle"
          >
            Search
          </button> -->
        </div>
      </form>
    </div>
  </header>
</template>

<script>
export default {
  name: "SearchComp",
  data() {
    return {
      apiQuery: "",
    };
  },
  props: {
    setDefault: Function,
    myHeaderStyle: {
      type: Object,
      required: false,
    },
    myBtnStyle: {
      type: Object,
      required: false,
    },
    imgSrc: {
      type: String,
      required: true,
    },
    imgAlt: {
      type: String,
      default: "gabrisca_img",
      required: false,
    },
    placeholder: {
      type: String,
      default: "Search...",
      required: false,
    },
  },
  methods: {
    // startSearch() {
    //   this.$emit("search", { text: this.textToSearch, type: "all" });
    //   this.textToSearch = "";
    // },
    sendQuery() {
      this.$emit("searchQuery", this.apiQuery);
      console.log(this.apiQuery);
      this.apiQuery = "";
    },
    resetAll() {
      this.apiQuery = ""; //svuoto il campo di ricerca
      this.setDefault(); //richiamo la funzione setDefault passata come props
    },
  },
};
</script>

<style lang="scss" scoped>
header {
  height: 65px;
  justify-content: space-between;

  @media all and (max-width: 1400px) {
  }

  @media all and (max-width: 1200px) {
  }

  @media all and (max-width: 1100px) {
  }

  @media all and (max-width: 992px) {
    justify-content: center;
  }

  @media all and (max-width: 600px) {
  }

  @media all and (max-width: 450px) {
  }
  .logo {
    // color: $color-btn;
    text-transform: uppercase;
    font-weight: 700;
    margin: 5px;
    margin-right: 10px;
    .bool_btn {
      background: transparent;
      border: transparent;
      &:hover {
        img {
          animation: shake 0.5s;
          animation-iteration-count: 5s;
        }
      }
      img {
        height: 100px;
        @media all and (max-width: 450px) {
          // height: 50px;
        }
        @media all and (max-width: 350px) {
          // height: 30px;
        }
        @media all and (max-width: 320px) {
          // display: none;
        }
      }
    }
    span {
      &:last-child {
        font-size: 2em;
      }
    }
  }
  .mc_btn {
    // background-color: $color-btn;
    border-color: transparent;
    margin-left: 3px;
    &:hover {
      filter: brightness(1.2);
    }
  }
}
@media only screen and (max-width: 450px) {
  button {
    &.mc_btn {
      display: none;
    }
  }
  div.logo span:first-child {
    display: block;
  }
  div.logo span:last-child {
    display: none;
  }
}
@media only screen and (min-width: 451px) {
  div.logo span:last-child {
    display: none;
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}
</style>
