<template lang="">
  <div id="details">
    <div class="top m-3">
      <h2>{{ detailCard.title }}</h2>
    </div>
      <b-container class="bv-example-row">
        <b-row class="mt-5">
          <b-col
            class="d-flex flex-column p-sm-1"
            sm="4"
            style="background-color: rgba(0, 0, 0, 0.4)"
          >
            <img
              class="img-fluid mb-2 mt-1"
              :src="getImg"
              :alt="detailCard.title"
            />
            <b-button
              :href="detailCard.game_url"
              target="blank"
              style="background-color: #9147ff"
              size="lg"
              class="mb-4"
            >
              Play Now <i class="fa-solid fa-play"></i>
            </b-button>
          </b-col>
          <b-col sm="8" style="background-color: rgba(0, 0, 0, 0.4)">
            <p>{{ detailCard.description }}</p>
          </b-col>
  
          <hr style="color: transparent" />
  
          <b-col
            sm="12"
            class="p-sm-3 mt-3 text-center"
            style="background-color: rgba(0, 0, 0, 0.4)"
          >
            <h3 class="mb-4">Additional Information</h3>
            <b-row class="mb-3">
              <b-col sm>
                <span class="tag">Developer: </span>
                <span>{{ detailCard.developer }}</span>
              </b-col>
              <b-col sm>
                <span class="tag">Publisher: </span>
                <span>{{ detailCard.publisher }}</span>
              </b-col>
              <b-col sm>
                <span class="tag">Genre: </span>
                <span>{{ detailCard.genre }}</span>
              </b-col>
            </b-row>
  
            <b-row class="mb-3">
              <b-col sm>
                <span class="tag">Release date: </span>
                <span>{{ detailCard.release_date }}</span>
              </b-col>
              <b-col sm>
                <span class="tag">Platform: </span>
                <span>{{ detailCard.platform }}</span>
              </b-col>
              <b-col sm>
                <span class="tag">Status: </span>
                <span>{{ detailCard.status }}</span>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
  
        <hr style="color: transparent" />
  
        <b-row>
          <b-col
            sm="12"
            class="p-sm-3 text-center"
            style="background-color: rgba(0, 0, 0, 0.4)"
          >
            <h3 class="mb-2">{{ detailCard.title }} Screenshots</h3>
          </b-col>
          <b-col
            sm
            class="p-sm-2"
            style="background-color: rgba(0, 0, 0, 0.4)"
            v-for="(screenshot, key) in detailCard.screenshots"
            :key="key.id"
          >
            <b-img
              fluid
              class="mb-2"
              :src="screenshot.image"
              :alt="detailCard.title + ' screenshot'"
            ></b-img>
          </b-col>
        </b-row>
  
        <hr style="color: transparent" />
  
        <b-row class="mb-4">
          <b-col
            sm="12"
            class="p-sm-3 text-center"
            style="background-color: rgba(0, 0, 0, 0.4)"
          >
            <h3 class="mb-2">Minimum System Requirements</h3>
          </b-col>
  
          <b-col
            sm="12"
            class="p-sm-2 text-center"
            style="background-color: rgba(0, 0, 0, 0.4)"
            v-for="(value, name, key) in detailCard.minimum_system_requirements"
            :key="key.id"
          >
            <span class="tag">{{ name }}: </span>
            <span>{{ value }}</span>
          </b-col>
        </b-row>
      </b-container>
  </div>
</template>
<script>
export default {
  name: "DetailComp",
  data() {
    return {
      description: [],
    };
  },
  props: {
    detailCard: Object,
  },
  computed: {
    getImg() {
      if (this.detailCard.thumbnail === null) {
        return require("@/assets/img/gamesdb/noimage.png");
      } else {
        return this.getUrl();
      }
    },
  },
  methods: {
    getUrl() {
      let id = this.detailCard.id;
      let url = `https://www.freetogame.com/g/${id}/thumbnail.jpg`;
      return url;
    }
  },
};
</script>
<style lang="scss" scoped>
#details {
  min-height: 100vh;
  width: 100vw;
  color: #fff;
  background: linear-gradient(
      90deg,
      #161718 0%,
      rgba(35, 46, 46, 0.1) 16.15%,
      rgba(35, 46, 46, 0.1) 80.21%,
      #161718 100%
    ),
    linear-gradient(
      180deg,
      rgb(39, 43, 48) 0%,
      rgba(0, 0, 0, 0.71) 33.85%,
      #292e33 100%
    );
  padding: 0 110px 110px 110px;

  @media all and (max-width: 1400px) {
    padding: 0 100px 100px 100px;
  }

  @media all and (max-width: 1200px) {
    padding: 0 80px 80px 80px;
  }

  @media all and (max-width: 1100px) {
    padding: 0 70px 70px 70px;
  }

  @media all and (max-width: 900px) {
    padding: 0 60px 60px 60px;
  }

  @media all and (max-width: 600px) {
    padding: 0 50px 50px 50px;
  }

  @media all and (max-width: 450px) {
    padding: 0 30px 50px 30px;
  }

  .top {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .tag {
    color: #aaaaaa;
  }
}
</style>
