<template lang="">
  <div 
    id="card"
    @click.prevent="$emit('readMore', card.id)"
  >
    <img 
      class="img-fluid" 
      :src="imgFunction" 
      :alt="card.title" 
    />
    <div class="developer">
      <span :style="cardInfoBg">
        {{ card.title }}
      </span>
    </div>
    <div class="caption">
      <h2>{{ card.developer}}</h2>
      <p>{{ card.genre }}</p>
    </div>
    <div class="overlay">
      <span>Read More</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "CardComp",
  data() {
    return {};
  },
  props: {
    card: Object,
    cardInfoBg: Object
  },
  computed: {
    imgFunction() {
      if (this.card.thumbnail === null) {
        return require("@/assets/img/gamesdb/noimage.png");
      } else {
        return this.card.thumbnail;
      }
    },
  },
  methods: {
    substringText(string, min, max) {
      return string.substring(min, max);
    },
  },
};
</script>
<style lang="scss" scoped>
#card {
  position: relative;
  height: 325px;
  margin: 20px;
  background-color: #fefefe;
  border: 1px solid #ececec;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: 280px;
  text-align: center;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  transition: all .8s ease-in;

  &:hover {
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    transform: scale(1.02);
  }

  @media all and (max-width: 1600px) {
    flex-basis: 320px;
  }

  @media all and (max-width: 1400px) {
    flex-basis: 340px;
  }

  @media all and (max-width: 1340px) {
    height: 220px;
    flex-basis: 200px;
  }

  @media all and (max-width: 1100px) {
  }

  @media all and (max-width: 900px) {
  }

  @media all and (max-width: 600px) {
    height: 220px;
    flex-basis: 100%;
  }

  @media all and (max-width: 450px) {
  }

  img {
    width: 100%;
    height: 70%;
    margin: auto;
  }

  .developer {
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      font-size: .8rem;
      font-weight: 900;
      text-transform: uppercase;
      padding: 3px 0;
      color: #fffefeb6;
    }
  }
  .caption {
    position: absolute;
    bottom: 0;
    height: 20%;
    width: 100%;
    background: #fefefeef;
    padding-top: 5px;
    text-align: left;
    padding: 10px 0;

    h2,
    p {
      margin: 0 3px 0 3px;
    }
    h2 {
      font-size: 0.9rem;
      font-weight: 600;
      color: #555555;
      @media all and (max-width: 1340px) {
font-size: .8rem;
  }
    }
    p {
      font-size: 0.8rem;
      font-weight: 600;
      color: #636161;
      @media all and (max-width: 1340px) {
font-size: .6rem;
  }
    }
  }

  .overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0);
    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -o-transition: opacity 1s ease-in-out;
    transition: opacity 1.8s ease-in-out;

    &:hover {
      cursor: pointer;
      opacity: 1;
      background: rgb(2, 0, 36);
      background: linear-gradient(
        36deg,
        rgba(2, 0, 36, 1) 0%,
        rgba(145, 71, 255, 1) 35%,
        rgba(0, 212, 255, 1) 100%
      );
      filter: brightness(1.4) opacity(0.9);
      
      span {
        display: block;
      }
    }

    span {
      display: none;
      color: #ececec;
      font-weight: 900;
      font-size: 2rem;
      text-shadow: 1px 1px 1px #000, 3px 3px 5px rgb(0, 0, 0);
    }
  }
}
