<template lang="">
  <div id="main">
    <div>
      <Pagination
        v-if="welcome"
        :btn-color="btnColor"
        :btn-page-color="btnPageColor"
        :page-number="pageNumber"
        :pages="pages"
        :page="page"
        :perPage="perPage"
        @pagPrec="pagPrec"
        @pagSucc="pagSucc"
        @firstPage="firstPage"
        @lastPage="lastPage"
        @currentPage="currentPage"
        @setmainList="setmainList"
        :paginateList="paginateList"
      />
      <div v-if="welcome" id="popular">
        <h2>popular games right now</h2>
        <div
          v-if="loading"
          class="loading_card"
        >
          <hollow-dots-spinner
            :animation-duration="1000"
            :dot-size="15"
            :dots-num="3"
            color="#9147ff"
          />
        </div>
        <div 
          v-if="!loading" 
          class="flex_center"
        >
          <Card
            @readMore="readMore"
            :card="card"
            v-for="card in mainList"
            :key="card.id"
            :card-info-bg="{
              backgroundColor: bgColor,
              height: '10%',
              width: '100%',
            }"
          />
        </div>
      </div>
      <div v-else class="flex_center">
        <div
          v-if="loading"
          class="loading_details"
        >
          <hollow-dots-spinner
            :animation-duration="1000"
            :dot-size="15"
            :dots-num="3"
            color="#9147ff"
          />
        </div>
        <div v-if="!loading" class="flex_center">
          <DetailCard 
            :detail-card="detailCard" 
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Card from "./Card.vue";
import DetailCard from "./DetailCard.vue";
import Pagination from "../SharedComponent/Pagination.vue";
import { HollowDotsSpinner } from "epic-spinners";

export default {
  name: "MainComp",
  components: {
    Card,
    DetailCard,
    Pagination,
    HollowDotsSpinner,
  },
  data() {
    return {
      bgColor: "#9147ff",
      btnColor: "btn-warning",
      btnPageColor: "btn-outline-warning",
    };
  },
  props: {
    welcome: {
      type: Boolean,
      default: true,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    mainList: Array,
    detailCard: Object,
    pageNumber: Number,
    pages: Array,
    page: Number,
    perPage: Number,
  },

  methods: {
    pagPrec() {
      this.$emit("pag-prec");
    },
    pagSucc() {
      this.$emit("pag-succ");
    },
    firstPage() {
      this.$emit("first-page");
    },
    lastPage() {
      this.$emit("last-page");
    },
    currentPage(currentPage) {
      this.$emit("current-page", currentPage);
    },
    setmainList() {
      this.$emit("set-main-list");
    },
    paginateList(mainList) {
      this.$emit("paginate-list", mainList);
    },
    readMore(id) {
      this.$emit("read-more", id);
    },
  },
};
</script>
<style lang="scss" scoped>
#main {
  color: #000000;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  #popular {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 150px;
    h2 {
      margin-top: 5px;
      text-transform: uppercase;
    }
    padding: 0 110px 0px 110px;

    @media all and (max-width: 1400px) {
      padding: 0 100px 0px 100px;
    }

    @media all and (max-width: 1200px) {
      padding: 0 80px 0px 80px;
    }

    @media all and (max-width: 1100px) {
      padding: 0 70px 0px 70px;
    }

    @media all and (max-width: 900px) {
      padding: 0 60px 0px 60px;
    }

    @media all and (max-width: 600px) {
      padding: 0 50px 0px 50px;
    }

    @media all and (max-width: 450px) {
      padding: 0 30px 0px 30px;
    }
  }

  .flex_center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  .loading_card,
  .loading_details {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .loading_card {
    height: 30vh;
    width: 100vw;
  }

  .loading_details {
    height: 50vh;
    width: 100vw;
  }
}
</style>
